@import url(https://cdnjs.cloudflare.com/ajax/libs/semantic-ui/2.4.1/semantic.min.css);
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  font-family: Open Sans,sans-serif;
  background: #F5F5FA;
  font-size: 16px;
}
/* hero */

.hero-container {
    max-height: 250px;
    line-height: 250px;
    overflow: hidden;
    position:relative;
}

.hero-img {
    max-width: 100%;
    max-height: 100%;
    display: block;
    opacity: 30%;
    /* position: absolute; */
}

.text-container {
    background-color: #ffffff;
    width: 200px;
    height: 200px;
}

.hero-text {
    z-index: 100;
    position: absolute;
    left: 30%;
    top: 50%; 
    transform: translate(-50%, -50%);
}

#hero-btn {
    padding: 10px 12px;
    z-index: 100;
    position: absolute;
    left: 15%;
    top: 75%; 
    transform: translate(-50%, -50%);
    background-color: #1964d4;
    border: 0;
}

/* lessonCard */
#1, #2, #3 {
    
}

/* category */

#category {
    padding-top: 10px;
    padding-right: 10px;
    padding-left: 0;
}

.category {
    border: 0.5px solid #808080;
    border-radius: 0.2em;
    height: 70px;
    line-height: 5;
}

.category:hover {
    background-color: #1964d4;
    border: none;
    color: #ffffff;
}

/* content */

.content-box {
    height: 100px;
    padding-top: 20px;
    
}

.content {
    border-top: 0.1px solid rgba(0,0,0,.15);
    border-bottom: 0.1px solid rgba(0,0,0,.15);
}

/* banner */

.banner-container {
    background-color: #ffffff;
    /* background-color: #1964d4; */
    height: 100px;
    line-height: 100px;
}

.banner-blue-container {
    background-color: #1964d4;
}

.banner-text {
    line-height: 3.4;
}

/* chatbot */
body {
    font-family: Garamond;
}

h1 {
    color: black;
    margin-bottom: 0;
    margin-top: 0;
    text-align: center;
    font-size: 40px;
}

h3 {
    color: black;
    font-size: 20px;
    margin-top: 3px;
    text-align: center;
}

#chatbox {
    margin-left: auto;
    margin-right: auto;
    width: 40%;
    margin-top: 60px;
}

#userInput {
    margin-left: auto;
    margin-right: auto;
    width: 40%;
    margin-top: 60px;
}

#textInput {
    width: 87%;
    border: none;
    border-bottom: 3px solid #009688;
    font-family: monospace;
    font-size: 17px;
}

#buttonInput {
    padding: 3px;
    font-family: monospace;
    font-size: 17px;
}

.userText {
    color: white;
    font-family: monospace;
    font-size: 17px;
    text-align: right;
    line-height: 30px;
}

.userText span {
    background-color: #009688;
    padding: 10px;
    border-radius: 2px;
}

.botText {
    color: white;
    font-family: monospace;
    font-size: 17px;
    text-align: left;
    line-height: 30px;
}

.botText span {
    background-color: #EF5350;
    padding: 10px;
    border-radius: 2px;
}

#tidbit {
    position:absolute;
    bottom:0;
    right:0;
    width: 300px;
}


@media (max-width: 996px){

    #hero-btn {
        left: 11%;
    }
}


